import { Message } from 'semantic-ui-react';

function Reporte({ stock = 'Ninguno', masVendido = 'Ninguno' }) {
  return (
    <Message
      info>
      <Message.Header>Reporte Principales</Message.Header>
      <p><strong>Producto con mas stock:</strong> { stock }</p>
      <p><strong>Producto mas vendido:</strong> { masVendido }</p>
    </Message>
  )
}

export { Reporte };