import React, { useState, useEffect } from 'react';
import { Container, Grid } from 'semantic-ui-react';
import { ToastContainer, toast } from 'react-toastify';
import { FormProducto } from './components/Producto/FormProducto';
import { Reporte } from './components/Reporte/Reporte';
import { TableProducto } from './components/Producto/TableProducto';
import { PaginationProducto } from './components/Producto/PaginationProducto';
import { FormVenta } from './components/Venta/FormVenta';
import { apiGet, apiPost, apiDelete, apiPut } from './data/api';
import 'react-toastify/dist/ReactToastify.css';
import './css/index.css';

function App() {
  const [productos, setProductos] = useState([]);
  const [productosBySelect, setProductosBySelect] = useState([]);
  const [pagination, setPagination] = useState({ page: 1, totalPages: 1 });
  const [reporte, setReporte] = useState({ stock: 'Ninguno', masVendido: 'Ninguno' });
  const [loadingVenta, setLoadingVenta] = useState(false);
  const [loadingProducto, setLoadingProducto] = useState(false);
  const [loadingTable, setLoadingTable] = useState(false);
  const [typeSubmit, setTypeSubmit] = useState('POST');
  const [venta, setVenta] = useState({producto_id: '', cantidad_producto: ''});
  const [producto, setProducto] = useState({id: '', nombre: '', referencia: '', precio: '', peso: '', categoria: '', stock: ''});
  
  useEffect(() => {
    getProductos();
  }, []);
  
  const getProductos = (page) => {
    setLoadingTable(true);
    Promise.all([getProductosByParam(0, page), getProductosByParam(1)]).then((res) => {
      const productosByPag = res[0].productos.data;
      const dataPage = { page: res[0].productos.current_page, totalPages: res[0].productos.last_page };
      const dataReporte = { stock: res[0].stock[0].nombre || 'Ninguno', masVendido: res[0].venta[0].nombre_producto || 'Ninguno' };
      const allProductos = res[1].productos;
      const mapProductos = allProductos.map(item => {
        return { key: item.id, text: item.nombre, value: item.id };
      });
      setTimeout(() => {
        setProductos(productosByPag);
        setPagination(dataPage);
        setProductosBySelect(mapProductos);
        setReporte(dataReporte);
        setLoadingTable(false);
      }, 1000);
    }).catch(err => {
      toast.error('Algo extraño ocurrio aquí');
      setLoadingTable(false);
    });
  };

  const getProductosByParam = (params, page = 1) => {
    return apiGet(`productos/index/${params}?page=${page}`); 
  };

  const changePage = (page) => {
    getProductos(page);
  };

  const getProducto = (idProducto) => {
    setLoadingTable(true);
    apiGet(`productos/${idProducto}`).then(res => {
      setTimeout(() => {
        setLoadingTable(false);
        setTypeSubmit('PUT');
        if (res.producto !== null) {
          setProducto({
            id: res.producto.id, 
            nombre: res.producto.nombre, 
            referencia: res.producto.referencia, 
            precio: res.producto.precio, 
            peso: res.producto.peso, 
            categoria: res.producto.categoria, 
            stock: res.producto.stock});
        } else {
          toast.error('Producto no encontrado en la Base de Datos');
        }
      }, 2000);
    }).catch(err => {
      setLoadingTable(false);
      toast.error('Algo extraño ocurrio aquí');
    });
  };

  const submitProducto = (values) => {
    setLoadingProducto(true);
    if (typeSubmit === 'POST') {
      setProducto(values);
      apiPost('productos/store', values).then(res => {
        setTimeout(() => {
          setLoadingProducto(false);
          setProducto({id: '', nombre: '', referencia: '', precio: '', peso: '', categoria: '', stock: ''});
          toast.success(`El producto ${res.producto.nombre} ha sido registrado correctamente`);
          getProductos();
        }, 2000);
      }).catch(err => {
        setLoadingProducto(false);
        toast.error('Algo extraño ocurrio aquí');
      });
    } else if (typeSubmit === 'PUT') {
      apiPut(`productos/${producto.id}`, values).then(res => {
        setTimeout(() => {
          setLoadingProducto(false);
          setTypeSubmit('POST');
          setProducto({id: '', nombre: '', referencia: '', precio: '', peso: '', categoria: '', stock: ''});
          toast.success(`El producto ${res.producto.nombre} ha sido actualizado correctamente`);
          getProductos();
        }, 2000);
      }).catch(err => {
        setLoadingProducto(false);
        toast.error('Algo extraño ocurrio aquí');
      });
    }
  };

  const deleteProducto = (idProducto) => {
    setLoadingTable(true);
    apiDelete(`productos/${idProducto}`).then(res => {
      setTimeout(() => {
        setLoadingTable(false);
        toast.success(`El producto ${res.producto.nombre} ha sido eliminado correctamente`);
      }, 2000);
        getProductos();
    }).catch(err => {
      setLoadingTable(false);
      toast.error('Algo extraño ocurrio aquí');
    });
  };

  const submitVenta = (values) => {
    setVenta(values);
    setLoadingVenta(true);
    apiPost('ventas/store', values).then(res => {
      setTimeout(() => {
        setLoadingVenta(false);
        setVenta({ producto_id: '', cantidad_producto: ''});
        if (res.status) toast.success(res.message);
        else toast.error(res.message);
      }, 2000);
      getProductos();
    }).catch(err => {
      setLoadingVenta(false);
      toast.error('Algo extraño ocurrio aquí');
    });
  };

  return (
    <React.Fragment>
      <Container
        fluid
        className='row-main margin-container'>
        <ToastContainer toastClassName='font-main'></ToastContainer>
        <Grid>
          <Grid.Row>
            <Grid.Column width={8}>
              <FormProducto
                producto={producto}
                submitProducto={submitProducto}
                loading={loadingProducto}>
              </FormProducto>
            </Grid.Column>
            <Grid.Column width={8}>
              <FormVenta
                productos={productosBySelect}
                venta={venta}
                submitVenta={submitVenta}
                loading={loadingVenta}>
              </FormVenta>
              <Reporte 
                stock={reporte.stock}
                masVendido={reporte.masVendido} />
            </Grid.Column>
          </Grid.Row>
          <Grid.Row>
            <Grid.Column width={16}>
              <TableProducto
                productos={productos}
                getProducto={getProducto}
                deleteProducto={deleteProducto}
                loading={loadingTable}>
              </TableProducto>
            </Grid.Column>
          </Grid.Row>
          <Grid.Row>
            <Grid.Column 
              width={16}
              textAlign='center'>
              <PaginationProducto 
                page={pagination.page}
                totalPages={pagination.totalPages} 
                changePage={changePage} />
            </Grid.Column>
          </Grid.Row>
        </Grid>
      </Container>
    </React.Fragment>
  );
}

export default App;
